'use client';

import React, { useState } from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';
import VentaDetalleModal from './VentaDetalleModal';
import VentaResumen from './VentaResumen';
import { VentaServicio } from '../../../entities/venta';

const VentasTable = () => {
  const { ventas, eliminarVenta } = useVentas(); // suponiendo que tienes eliminarVenta()
  const [ventaSeleccionada, setVentaSeleccionada] = useState<VentaServicio | null>(null);
  const [filtroCliente, setFiltroCliente] = useState('');
  const [filtroFecha, setFiltroFecha] = useState('');

  const ventasFiltradas = ventas.filter(v =>
    v.cliente.nombre.toLowerCase().includes(filtroCliente.toLowerCase()) &&
    v.fechaRegistro.includes(filtroFecha)
  );

  return (
    <div className="space-y-6">
      <VentaResumen />

      {/* Filtros */}
      <div className="input-bar">
        <input
          type="text"
          placeholder="Buscar cliente"
          className="border p-2 rounded w-full md:w-1/3"
          value={filtroCliente}
          onChange={(e) => setFiltroCliente(e.target.value)}
        />
        <input
          type="date"
          className="border p-2 rounded w-full md:w-1/3"
          value={filtroFecha}
          onChange={(e) => setFiltroFecha(e.target.value)}
        />
      </div>

      {/* Tabla */}
      <div className="table-container">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th>ID</th>
              <th>Cliente</th>
              <th>Fecha</th>
              <th>Total</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {ventasFiltradas.length > 0 ? (
              ventasFiltradas.map((venta) => (
                <tr key={venta.idVentaServicio} className="hover:bg-gray-50">
                  <td>{venta.idVentaServicio}</td>
                  <td className="font-medium">{venta.cliente.nombre}</td>
                  <td>{formatDate(venta.fechaRegistro)}</td>
                  <td className="text-green-600 font-semibold">
                    {formatCurrency(venta.costoTotal)}
                  </td>
                  <td className="flex flex-col sm:flex-row gap-2 py-2">
                    <button
                      onClick={() => setVentaSeleccionada(venta)}
                      className="text-blue-600 hover:underline"
                    >
                      Ver detalle
                    </button>
                    <button
                      onClick={() => eliminarVenta?.(venta.idVentaServicio)}
                      className="text-red-600 hover:underline"
                    >
                      Eliminar
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={5} className="text-center text-gray-500 py-4">
                  No hay ventas registradas.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* Modal */}
      {ventaSeleccionada && (
        <VentaDetalleModal
          venta={ventaSeleccionada}
          onClose={() => setVentaSeleccionada(null)}
        />
      )}
    </div>
  );
};

export default VentasTable;
