'use client';
import React from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const VentaResumen = () => {
  const { ventas } = useVentas();
  const totalVentas = ventas.length;
  const totalIngresos = ventas.reduce((acc, v) => acc + v.costoTotal, 0);
  const totalDescuentos = ventas.reduce((acc, v) => acc + v.descuento, 0);

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
      <div className="bg-white p-4 rounded-lg shadow border">
        <h4 className="text-sm text-gray-500">üßæ Total Ventas</h4>
        <p className="text-2xl font-bold text-indigo-600">{totalVentas}</p>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <h4 className="text-sm text-gray-500">üí∞ Ingresos Totales</h4>
        <p className="text-2xl font-bold text-green-600">{formatCurrency(totalIngresos)}</p>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <h4 className="text-sm text-gray-500">üîª Descuentos</h4>
        <p className="text-2xl font-bold text-red-600">{formatCurrency(totalDescuentos)}</p>
      </div>
    </div>
  );
};

export default VentaResumen;
