'use client';
import React, { useState } from 'react';
import { Cliente, DetalleServicio, ProductoDetalleVenta, VentaServicio } from '@/entities/venta';
import { useVentas } from '@/hooks/useVentas';
import { formatCurrency } from '@/lib/logistica/formatters';
import { Dialog, DialogContent, DialogTrigger } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';

const clientes: Cliente[] = [ /* ...clientesDummy como antes */ ];
const servicios = [ /* ...serviciosDisponibles como antes */ ];

export default function VentaRegistroModal() {
  const { registrarVenta } = useVentas();
  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);
  const [open, setOpen] = useState(false);

  const agregarServicio = (idServicio: string) => {
    const servicio = servicios.find(s => s.idServicio === idServicio);
    if (!servicio) return;
    const detalle: DetalleServicio = {
      idDetalleServicio: `DET-${Date.now()}`,
      idServicio: servicio.idServicio,
      nombreServicio: servicio.nombre,
      costoTotal: servicio.costo,
      productos: [] as ProductoDetalleVenta[],
    };
    setDetalles(prev => [...prev, detalle]);
  };

  const eliminarDetalle = (idDetalle: string) => {
    setDetalles(prev => prev.filter(d => d.idDetalleServicio !== idDetalle));
  };

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) return;
    const total = detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento;
    const venta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente,
      detalles,
      costoTotal: total,
      descuento,
      fechaRegistro: new Date().toISOString(),
    };
    registrarVenta(venta);
    setOpen(false);
    setCliente(null);
    setDetalles([]);
    setDescuento(0);
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button className="bg-indigo-600 text-white">‚ûï Registrar Venta</Button>
      </DialogTrigger>
      <DialogContent className="max-w-3xl">
        <h2 className="text-lg font-semibold mb-4">Registrar nueva venta</h2>

        {/* Cliente */}
        <select
          className="w-full border p-2 rounded mb-3"
          value={cliente?.idCliente || ''}
          onChange={(e) => {
            const c = clientes.find(c => c.idCliente === e.target.value);
            if (c) setCliente(c);
          }}
        >
          <option value="">Seleccionar cliente</option>
          {clientes.map((c) => (
            <option key={c.idCliente} value={c.idCliente}>
              {c.nombre} - {c.correo}
            </option>
          ))}
        </select>

        {/* Servicios disponibles */}
        <div className="grid grid-cols-2 gap-2">
          {servicios.map(s => (
            <Button key={s.idServicio} variant="outline" onClick={() => agregarServicio(s.idServicio)}>
              {s.nombre} ({formatCurrency(s.costo)})
            </Button>
          ))}
        </div>

        {/* Detalles a√±adidos */}
        <ul className="mt-4 space-y-2">
          {detalles.map((d) => (
            <li key={d.idDetalleServicio} className="border rounded p-3 flex justify-between">
              <span>{d.nombreServicio}</span>
              <Button variant="ghost" onClick={() => eliminarDetalle(d.idDetalleServicio)}>Quitar</Button>
            </li>
          ))}
        </ul>

        {/* Descuento y total */}
        <div className="mt-4 space-y-2">
          <input
            type="number"
            placeholder="Descuento"
            className="border p-2 rounded w-full"
            value={descuento}
            onChange={(e) => setDescuento(Number(e.target.value))}
          />
          <p className="text-right text-green-600 font-bold">
            Total: {formatCurrency(detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento)}
          </p>
        </div>

        {/* Botones */}
        <div className="mt-4 flex justify-end gap-2">
          <Button variant="secondary" onClick={() => setOpen(false)}>Cancelar</Button>
          <Button onClick={handleSubmit} disabled={!cliente || detalles.length === 0}>
            Registrar
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
