'use client';

import React, { useState } from 'react';
import { VentaServicio, Cliente, DetalleServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientesDummy: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'ABC Soluciones', telefono: '987654321', correo: 'abc@empresa.com' },
  { idCliente: 'CL-002', nombre: 'XYZ Market', telefono: '999888777', correo: 'xyz@empresa.com' },
];

const serviciosDisponibles = [
  { idServicio: 'SVC-001', nombre: 'Instalaci√≥n de red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte t√©cnico', costo: 120 },
  { idServicio: 'SVC-003', nombre: 'Cableado estructurado', costo: 300 },
];

const VentaForm = () => {
  const { registrarVenta } = useVentas();
  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);
  const [mensaje, setMensaje] = useState('');
  const [error, setError] = useState('');

  const agregarServicio = (id: string) => {
    if (detalles.some((d) => d.idServicio === id)) return;
    const servicio = serviciosDisponibles.find((s) => s.idServicio === id);
    if (servicio) {
      const detalle: DetalleServicio = {
        idDetalleServicio: `DET-${Date.now()}`,
        idServicio: servicio.idServicio,
        nombreServicio: servicio.nombre,
        costoTotal: servicio.costo,
      };
      setDetalles((prev) => [...prev, detalle]);
    }
  };

  const eliminarServicio = (id: string) => {
    setDetalles((prev) => prev.filter((d) => d.idServicio !== id));
  };

  const limpiar = () => {
    setCliente(null);
    setDetalles([]);
    setDescuento(0);
    setMensaje('');
    setError('');
  };

  const total = detalles.reduce((acc, s) => acc + s.costoTotal, 0) - descuento;

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) {
      setError('Selecciona un cliente y al menos un servicio');
      return;
    }
    const venta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: new Date().toISOString(),
    };
    registrarVenta(venta);
    limpiar();
    setMensaje('‚úÖ Venta registrada con √©xito');
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
      {/* Panel principal */}
      <div className="md:col-span-2 space-y-4">
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-1">Cliente</label>
          <select
            className="border p-2 rounded w-full"
            value={cliente?.idCliente || ''}
            onChange={(e) => {
              const selected = clientesDummy.find(c => c.idCliente === e.target.value);
              if (selected) setCliente(selected);
            }}
          >
            <option value="">Seleccionar cliente</option>
            {clientesDummy.map(c => (
              <option key={c.idCliente} value={c.idCliente}>
                {c.nombre} - {c.correo}
              </option>
            ))}
          </select>
          {cliente && (
            <div className="mt-2 text-sm text-gray-600">
              üìû {cliente.telefono} ‚Äì ‚úâÔ∏è {cliente.correo}
            </div>
          )}
        </div>

        <div>
          <p className="text-sm font-semibold mb-2">Servicios disponibles:</p>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
            {serviciosDisponibles.map((s) => (
              <button
                key={s.idServicio}
                onClick={() => agregarServicio(s.idServicio)}
                className="border border-gray-300 rounded-lg p-3 text-left hover:bg-gray-100 transition"
              >
                <p className="font-medium">{s.nombre}</p>
                <p className="text-sm text-gray-500">{formatCurrency(s.costo)}</p>
              </button>
            ))}
          </div>
        </div>

        <div>
          <p className="text-sm font-semibold mb-2">Servicios a√±adidos:</p>
          {detalles.length === 0 && (
            <p className="text-sm text-gray-400">Ning√∫n servicio a√±adido a√∫n.</p>
          )}
          <ul className="space-y-2 text-sm">
            {detalles.map((d) => (
              <li key={d.idDetalleServicio} className="flex justify-between items-center border rounded px-3 py-2">
                <span>{d.nombreServicio}</span>
                <div className="flex gap-4 items-center">
                  <span className="text-gray-600">{formatCurrency(d.costoTotal)}</span>
                  <button onClick={() => eliminarServicio(d.idServicio)} className="text-red-500 hover:underline">
                    Quitar
                  </button>
                </div>
              </li>
            ))}
          </ul>
        </div>
      </div>

      {/* Panel lateral de resumen */}
      <div className="bg-white rounded-xl border shadow p-4 space-y-4">
        <h3 className="text-lg font-bold text-gray-800">Resumen de venta</h3>

        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-1">Descuento (S/.)</label>
          <input
            type="number"
            className="border p-2 rounded w-full"
            value={descuento}
            min={0}
            onChange={(e) => setDescuento(Number(e.target.value))}
          />
        </div>

        <div className="text-sm text-gray-600">
          <p>Subtotal: {formatCurrency(detalles.reduce((acc, s) => acc + s.costoTotal, 0))}</p>
          <p>Descuento: {formatCurrency(descuento)}</p>
          <hr className="my-2" />
          <p className="text-lg font-bold text-green-700">Total: {formatCurrency(total)}</p>
        </div>

        {mensaje && <p className="text-green-600 text-sm">{mensaje}</p>}
        {error && <p className="text-red-600 text-sm">{error}</p>}

        <div className="flex gap-3">
          <button
            onClick={handleSubmit}
            disabled={!cliente || detalles.length === 0}
            className="bg-blue-600 text-white px-4 py-2 rounded w-full disabled:bg-gray-400"
          >
            Registrar
          </button>
          <button
            onClick={limpiar}
            className="bg-gray-100 text-gray-800 px-4 py-2 rounded w-full"
          >
            Cancelar
          </button>
        </div>
      </div>
    </div>
  );
};

export default VentaForm;
